.appBodyContainer {
    height: 100%
}

.middleColumn {
    display: flex;
    justify-content: center;
}

.rightColumn {
    position: absolute;
    width: 100vw;
    top: 0;
    border-left: 1px solid rgb(90, 90, 90);
}

.displayNone {
    display: none;
}

.menuBtn{
    width: 100%;
}

.menuCardLabel {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #c4c4c4;
    padding: 20px;
}

.menuCardExpanded {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #c4c4c4;
    padding: 20px 20px 20px 40px;
}

.menuCardExpandedLabel {
}

@media only screen and (min-width: 992px) {
    .appBodyContainer {
        display: grid;
        grid-template-columns: 150px auto 300px;
    }

    .rightColumn {
        position: static;
        width: 300px;
        border-left: 1px solid rgb(90, 90, 90);
    }
}

